<script>
import vue, {the, lib, library} from "vue.min.js"
export default vue.router ({
	id: "cpanel.html",
	setup: function () {
		vue.watch (vue.on.progress ("reference"), function () {
			if (the.account.on.line ()) {}
			else vue.router.go ("account/login.html")
			})
		return vue.export ()
		},
	method: {
		function () {},
		toast () {},
		notification () {},
		},
	})
</script>

<template id="cpanel.html" url="/cpanel" type="router">
	<media-query:progress v-if="vue.on.progress ()"/>
	<layout slot="the:web-view" v-else>
		<div v-if="the.account.on.line ()" class="flex-grow">
			<dom v-if="the.account.access ('investor')" slot="the:page/investor"/>
			<dom v-if="the.account.access ('sales')" slot="the:page/sales"/>
		</div>
		<div class="flex-grow" v-else></div>
	</layout>
</template>
